.input-section
{
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5px;
}
.btn
{
    padding: 5px;
    border-radius: 2px;
    cursor: pointer;
    width: 10%;
    font-size: large;
    border: 1px solid grey;
    background: #0000ff;
    color: white;
    font-weight: 500;
}

.btn:hover
{
    background: #0000ff;
    box-shadow: 0px 0px 1px black;
}

.input-tab
{
    padding: 5px;
    border-radius: 2px;
    width: 60%;
    background: whitesmoke;
    border: 1px solid grey;
}