.inner-div
{
    margin-top: 10px;
    border: 3px solid whitesmoke;
    display: flex;
    justify-content: space-between;
    width: 80%;
    height: auto;
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 10px;
    background: rgb(234, 234, 234);
}

.btn1
{
    padding: 5px;
    border-radius: 2px;
    cursor: pointer;
    width: 10%;
    height: 60%;
    font-size: large;
    border: 1px solid grey;
    background: #0000ff;
    color: white;
    font-weight: 500;
    align-self: center;
}

.todos-section
{
    width: 100%;
    height: 350px;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.todos-section::-webkit-scrollbar {
    width: 0.5em;
}